



























































import { Vue, Component, Prop, Emit } from "vue-property-decorator";
@Component
export default class UUSLayer extends Vue {
  //显示弹层;
  @Prop({
    type: Boolean,
    required: true,
    default: false
  })
  private popupSwitch!: boolean;
  //取消;
  @Prop({
    type: Object,
    default: () => {
      return {
        show: true,
        className: "u-layer-btnCur",
        style: {},
        text: "取消"
      };
    }
  })
  private cancelBtn!: object;
  //确定
  @Prop({
    type: Object,
    default: () => {
      return {
        show: true,
        className: "u-layer-btnCur",
        style: {},
        text: "确定"
      };
    }
  })
  private ensureBtn!: object;
  //遮罩
  @Prop({
    type: Object,
    default: () => {
      return {
        show: true,
        className: "",
        style: {}
      };
    }
  })
  private maskLayer!: object;
  //弹层
  @Prop({
    type: Object,
    default: () => {
      return {
        id: "",
        className: "",
        style: {}
      };
    }
  })
  private layer!: object;
  //标题
  @Prop({
    type: Object,
    default: () => {
      return {
        id: "",
        className: "",
        style: {},
        text: ""
      };
    }
  })
  private title!: object;
  //标题
  @Prop({
    type: Object,
    default: () => {
      return {
        className: "",
        style: {},
        text: ""
      };
    }
  })
  private center!: object;

  @Emit("handleCancel")
  handleCancel() {
    return;
  }

  @Emit("handleEnsure")
  handleEnsure() {
    return;
  }

  @Emit("handlemMask")
  handlemMask() {
    return;
  }
}
